import React from 'react';

const ActionProvider = ({ createChatBotMessage, setState, children }) => {
  const handleAbout = () => {
    const botMessage = createChatBotMessage('Je suis Chauffour Evan, Actuellement en BUT Mtier du Multimdia et de l\'internet');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  const handleSkills = () => {
    const botMessage = createChatBotMessage('Je suis juste trop chaud');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  const handleHobbies = () => {
    const botMessage = createChatBotMessage('Je suis passion de sport et plus prcisment de basket, de cinma et surtout de dveloppement web.');

    setState((prev) => ({
      ...prev,
      messages: [...prev.messages, botMessage],
    }));
  };

  return (
    <div>
      {React.Children.map(children, (child) => {
        return React.cloneElement(child, {
          actions: {
            handleAbout,
            handleSkills,
            handleHobbies,
          },
        });
      })}
    </div>
  );
};

export default ActionProvider;