import { createChatBotMessage } from 'react-chatbot-kit';
import Options from "./Options/options";

const botName = 'Evan';

const config = {
    botName: botName,
  initialMessages: [createChatBotMessage(`Salut ! Moi c'est ${botName}. Comment puis-je t'aider ?`,{
    widget: "options",
  })
],
widgets: [
    {
      widgetName: "options",
      widgetFunc: (props) => <Options {...props} />,
    },
  ],
};

export default config;